import React, { useState } from 'react'

let renderCount = 0;

export const Child = () => {
  const [x, setX] = useState(0);
  const [y, setY] = useState({});

  renderCount += 1;

  return <div>
    render count: {renderCount}
    <hr />
    <button onClick={() => { setX(1); }}>setX(change)</button>
    <button onClick={() => { setX(x); }}>setX(same)</button>
    {" "}
    <button onClick={() => { setY({ x: 0 }); }}>setY(change)</button>
    <button onClick={() => { setY(y); }}>setY(sameRef)</button>
    <button onClick={() => { setY(JSON.parse(JSON.stringify(y))); }}>setY(sameVal)</button>
  </div>
}
